.wp-block-lf-iframe-embed {
  padding: 1rem;
  position: relative; }

.iframe-overlay {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 99; }

.url-warning {
  background-color: orange;
  margin-bottom: 1rem;
  padding: 0.2rem;
  margin-top: -1rem;
  color: black; }
